import React, { useEffect } from 'react'
var DateFormat = require("dateformat");
const EconomicOverviewChart = (props) => {
  const getChartData = () => {
    Promise.all([
      import("@amcharts/amcharts4/core"),
      import("@amcharts/amcharts4/charts"),
      import("@amcharts/amcharts4/themes/animated"),
    ]).then((modules) => {
	  let am4core = modules[0];
	  let am4charts = modules[1];
	  am4core.addLicense("CH259299157");
	  let chart = am4core.create(props.graphId, am4charts.XYChart);
	  chart.paddingRight = 20;
	  let data = props.data; 
    //console.log(props.graphtype,'graphtype----');


    chart.data = data;
     chart.numberFormatter.numberFormat = "#a";
    chart.numberFormatter.bigNumberPrefixes = [
    { number: 1000, prefix: "K" },
    { number: 1000000, prefix: "M" },
    { number: 1000000000, prefix: "B" },
    { number: 1000000000000, prefix: "T" },
    { number: 1000000000000000, prefix: "P" },
    { number: 1000000000000000000, prefix: "E" },
    { number: 1e21, prefix: "Z" },
    { number: 1e24, prefix: "Y" },
    ];


    var categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
    //categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.dataFields.category = "name";
    //categoryAxis.renderer.minGridDistance = 1;
    categoryAxis.renderer.inversed = true;
    categoryAxis.renderer.grid.template.disabled = true;
    //categoryAxis.renderer.inversed = true;
    var valueAxis = chart.xAxes.push(new am4charts.ValueAxis());
    valueAxis.min = 0;

    valueAxis.fontSize = 10;
    // valueAxis.dashLength = 10;
    // valueAxis.tickLength = 10;
    // valueAxis.gridAlpha = 10;
    var series = chart.series.push(new am4charts.ColumnSeries());
    series.dataFields.categoryY = "name";
    series.dataFields.valueX = "latestdata";
   // series.tooltipText = "{valueX.value}"
    //series.columns.template.strokeOpacity = 0;
    //series.columns.template.column.cornerRadiusBottomRight = 5;
   // series.columns.template.column.cornerRadiusTopRight = 5;
    series.columns.template.propertyFields.fill = 'color';
    series.columns.template.propertyFields.stroke = 'color';
    series.columns.template.tooltipText = "{name}: {displaydata}";

    var labelBullet = series.bullets.push(new am4charts.LabelBullet())
    labelBullet.label.horizontalCenter = "left";
    labelBullet.label.dx = 10;
    //labelBullet.label.text = "{values.valueX.workingValue.formatNumber('#.0as')}";
    labelBullet.locationX = 1;

    // // as by default columns of the same series are of the same color, we add adapter which takes colors from chart.colors color set
    // series.columns.template.adapter.add("fill", function(fill, target){
    // return chart.colors.getIndex(target.dataItem.index);
    // });
    
    
  chart.current = chart;
  return () => {
    chart.dispose();
  }
    })
  }
  useEffect(() => {
    getChartData();
  }, [])
  return (
  	
    <div>
      <div id={props.graphId} style={{  height: "400px" }}></div>
    </div>
  )
}
export default EconomicOverviewChart;