import React, { Component } from 'react'  
import * as am4core from "@amcharts/amcharts4/core";  
import * as am4charts from "@amcharts/amcharts4/charts";  
import am4themes_animated from "@amcharts/amcharts4/themes/animated";  

export default class ExistingHomeChart extends Component {  
  
    componentDidMount() {  
        am4core.useTheme(am4themes_animated);
        // Create chart instance
        var chart = am4core.create("3748", am4charts.XYChart);
        chart.numberFormatter.numberFormat = "#a";
        chart.numberFormatter.bigNumberPrefixes = [
        { number: 1000, prefix: "K" },
        { number: 1000000, prefix: "M" },
        { number: 1000000000, prefix: "B" },
        { number: 1000000000000, prefix: "T" },
        { number: 1000000000000000, prefix: "P" },
        { number: 1000000000000000000, prefix: "E" },
        { number: 1e21, prefix: "Z" },
        { number: 1e24, prefix: "Y" },
        ];

        
        var data=this.props.new;
        //var cache1 = this.props.existing1;
        console.log(this.props.existing1,'this.props.existing1');

         var graphData = [];
        for (var index in data) {
        var date = index;
        data[index].date = date;      
        data[index].data1 = data[index].v;  
        data[index].data2 = this.props.new1[date]['v']; 
        graphData.unshift(data[index]);
        }

        

        console.log(graphData,'ExistingHomeChart-------')       

        chart.data =  graphData;
        var dateAxis = chart.xAxes.push(new am4charts.DateAxis());
       // dateAxis.renderer.grid.template.location = 10;
        dateAxis.renderer.grid.template.disabled = true;
        var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
        // Create series
        function createSeries(field, name, hiddenInLegend) {
        var series = chart.series.push(new am4charts.LineSeries());
        series.dataFields.valueY = field;
        series.dataFields.dateX = "date";
        series.strokeWidth = 2;
        series.yAxis = valueAxis;
       /// series.name = name;
        series.tooltipText = "{valueY}[/]";
        series.tensionX = 0.8;
        series.showOnInit = true;
        return series;
        }

      //  var series1 = createSeries("data1", "United States", true);
        var series2 = createSeries("data1");
        var series3 = createSeries("data2");

        chart.legend = new am4charts.Legend();
        chart.cursor = new am4charts.XYCursor();

    }  
  
    componentWillUnmount() {  
        if (this.chart) {  
            this.chart.dispose();  
        }  
    }  
    render() {  
        return (  
            <div>  
            <div style={{  height: "170px" }} id='3748'></div>
            </div>  
        )  
    }  
}