import React from 'react';
import { Row, Col, Card, Table } from 'react-bootstrap';
import TableCell from '@material-ui/core/TableCell';  
import TableContainer from '@material-ui/core/TableContainer';  
import TableHead from '@material-ui/core/TableHead';  
import TableRow from '@material-ui/core/TableRow'; 
export default class DataTable extends React.Component {
    
    constructor(props){
      super(props);
      this.getHeader = this.getHeader.bind(this);
      this.getRowsData = this.getRowsData.bind(this);
      this.getKeys = this.getKeys.bind(this);
    }
    
    getKeys = function(){
      return Object.keys(this.props.data);
    }
    
    getHeader = function(){
      var keys = this.getKeys();
      return keys.map((key, index)=>{
        return <th key={key}>{key.toUpperCase()}</th>
      })
    }


    
    getRowsData = function(){
      var items = this.props.data;
      var keys = this.getKeys();
      return items.map((row, index)=>{
        //return <tr key={index}><RenderRow key={index} data={row} keys={keys}/></tr>
        return <TableRow key={index}>  
                  <TableCell align="left">{row.name}</TableCell> 
                  <TableCell align="right">{row.share}</TableCell> 
                  <TableCell align="right">{row.lastdata}</TableCell>
                  <TableCell align="right">{row.latestdata}</TableCell>
                  <TableCell align="right">{row.change}</TableCell>
                  <TableCell align="right">{row.percentchange}</TableCell>     
              </TableRow>
      })
    }

    render() {
        return (
            <Table responsive>
            <TableHead>
            <TableRow><th colSpan={2} className="text-center">&nbsp;</th>
            <th colSpan={5} className="text-center">Trailing 12-Months</th>
            </TableRow>
            <TableRow>
            <th rowSpan={2} className="table-style">&nbsp;</th>
            <th rowSpan={2} className="share">Share of Total</th>                     
            <th>Apr '20</th>
            <th>Apr '21</th>
            <th>Net Change</th>
            <th>% Change</th>
            </TableRow>                                               
            </TableHead>
            <tbody>                               
            {this.getRowsData()}                                           
            </tbody>
            </Table>
        );
    }
}

const RenderRow = (props) =>{
  return props.keys.map((key, index)=>{
    return <td key={props.data[key]}>{props.data[key]}</td>
  })
}