					<div className="notes-area">
						<b>Note:</b>
						<ul>
							<li><span>Population: </span>The Bureau of Economic Analysis (BEA) uses the Census Bureau's midyear (July 1) population estimates.</li>
							<li><span>Labor Force: </span>The concepts and definitions underlying LAUS data come from the Current Population Survey (CPS), the household survey that is the official measure of the labor force for the nation. US Bureau of Labor Statistics.</li>
							<li><span>Employment: </span>The Current Employment Statistics (CES) survey utilizes payroll records and is designed to provide industry information on nonfarm wage and salary employment, average weekly hours and average hourly earnings for the nation, states and metropolitan areas. US Bureau of Labor Statistics.</li>
							<li><span>Unemployment: </span>Unemployment figures sourced to The US Department of Labor, Bureau of Labor Statistics (BLS), Local Area Unemployment Statistics. The BLS defines unemployment as the labor force unemployed or as all persons who, during the reference week (the week including the 12th day of the month) had no employment and were available for work, except for temporary illness, and had made specific efforts to find employment some time during the 4 week-period ending with the reference week.</li>
							<li><span>Unemployment Rate: </span>The concepts and definitions underlying LAUS data come from the Current Population Survey (CPS), the household survey that is the official measure of the labor force for the nation. US Bureau of Labor Statistics.</li>
							<li><span>Number of Occupied Housing Units: </span>Number of Occupied Housing Units sourced to Clark County Comprehensive Planning. Comprehensive Planning collaborates with other local agencies to prepare annual housing unit estimates for Clark County and the Cities.</li>
							<li><span>Persons per Household: </span> Persons per Household sourced to Clark County Comprehensive Planning and Applied Analysis Calculation. Applied Analysis used the Comprehensive Planning estimate of population for each area, and divided it by the corresponding estimate for the number of occupied housing units in that area.</li>
							<li><span>Number of Vacant Housing Units: </span>Number of Vacant Housing Units sourced to Clark County Comprehensive Planning. Comprehensive Planning collaborates with other local agencies to prepare annual housing unit estimates for Clark County and the Cities.</li>
							<li><span>Housing Vacancy Rate: </span>Housing Vacancy Rate sourced to Clark County Comprehensive Planning and Applied Analysis Calculation. Applied Analysis used the Comprehensive Planning estimate of vacant housing units for each area, and divided it by the corresponding estimate for the number of housing units (the sum of occupied and vacant) in that area.</li>
							<li><span>Jobs Per Household: </span>Jobs Per Household sourced to Clark County Comprehensive Planning, The US Department of Labor, Bureau of Labor Statistics (BLS), Local Area Unemployment Statistics and Applied Analysis Calculation. Applied Analysis used the BLS estimate of employment for each area, and divided it by the corresponding estimate for the Comprehensive Planning estimate of vacant housing units for that area. Note that this reflects the residency of the employee, i.e. a Henderson resident working in unincorporated Clark County would be counted as a job in Henderson.</li>
							<li><span>Median Income: </span>Median Income sourced to The US Census Bureau, The American Community Survey one year estimates. The ACS defines median income as the household income in the past 12 months. Values for the balance of Clark County estimated based on a weighted average.</li>
							<li><span>Average Annual Wages - Private: </span>Based on survey of covered employment. U.S. Bureau of Labor Statistics.</li>
							<li><span>Average Annual Wages - Manufacturing: </span>Based on survey of covered employment. U.S. Bureau of Labor Statistics.</li>
							<li><span>Vehicles Available: </span>Vehicles Available sourced to the US Census Bureau, The American Community Survey one year estimates. Values for the balance of Clark County estimated based on a weighted average.</li>
							<li><span>Estimated Vehicles per Household: </span>Estimated Vehicles per Household sourced to the US Census Bureau, The American Community Survey (ACS) one year estimate and Applied Analysis calculations. The total number of vehicles reported in the ACS for each area was divided by the corresponding number of households. Values for the balance of Clark County estimated based on a weighted average.</li>
							<li><span>Mean Travel Time to Work (minutes): </span>Mean Travel Time to Work (minutes) sourced to the US Census Bureau, The American Community Survey one year estimates. Values for the balance of Clark County estimated based on a weighted average.</li>
							<li><span>Number of Elementary Schools: </span>Number of Elementary Schools sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to calculate the number of elementary schools in each location.</li>
							<li><span>Number of Middle Schools: </span>Number of Middle Schools sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to calculate the number of middle schools in each location.</li>
							<li><span>Number of High Schools: </span>Number of High Schools sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to calculate the number of high schools in each location.</li>
							<li><span>Total Enrolment: </span>Total Enrolment sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to calculate the total number of students enrolled at schools in each location.</li>
							<li><span>School Index Score: </span>School Index Score sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual school index scores. An average of these scores, weighted by the enrollment of each school, was calculated for each area.</li>
							<li><span>Graduation Rates: </span>Graduation Rates sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual graduation rates. An average of these rates, weighted by the enrollment of each school, was calculated for each area.</li>
							<li><span>Performance Framework Average Star Rating: </span>School Index Score sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual school index scores. An average of these scores, weighted by the enrollment of each school, was calculated for each area.</li>
							<li><span>Percent Proficient - 3rd Grade Math: </span>Percent Proficient - 3rd Grade Math sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual proficiency rates. An average of these rates, weighted by the enrollment of each school, was calculated for each area.</li>
							<li><span>Percent Proficient - 3rd Grade Reading: </span>Percent Proficient - 3rd Grade Reading sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual proficiency rates. An average of these rates, weighted by the enrollment of each school, was calculated for each area.</li>
							<li><span>Percent Proficient 8th Grade Math: </span>Percent Proficient - 8th Grade Math sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual proficiency rates. An average of these rates, weighted by the enrollment of each school, was calculated for each area.</li>
							<li><span>Percent Proficient - 8th Grade Reading: </span>Percent Proficient - 8th Grade Reading sourced to Nevada School Performance Framework and Applied Analysis Calculations. Applied Analysis used location data to create a list of each areas schools and their individual proficiency rates. An average of these rates, weighted by the enrollment of each school, was calculated for each area.</li>								
						</ul>
					</div>
