import React, { useEffect } from 'react'
var DateFormat = require("dateformat");
const EconomicOverviewChart = (props) => {
  const getChartData = () => {
    Promise.all([
      import("@amcharts/amcharts4/core"),
      import("@amcharts/amcharts4/charts"),
      import("@amcharts/amcharts4/themes/animated"),
    ]).then((modules) => {
      let am4core = modules[0];
      let am4charts = modules[1];
      am4core.addLicense("CH259299157");
      let chart = am4core.create("economicoverview_chart", am4charts.XYChart);
      chart.paddingRight = 20;
      let data = props.data;
      var graphData = [];
     
     var bigestnum=0;
      for (var ind in data) {
        if(bigestnum < data[ind].data){
          bigestnum   = data[ind].data;
        }
      }

      var graphData = [];
      for (var index in data) {
        var date = DateFormat(data[index].date,"2020");
        var pupulation =data[index].data;
        data[index].name = index;
        data[index].date = date;  
        data[index].data = pupulation;
        if(pupulation==bigestnum){
          data[index].color = "#f4be4a";
        }else{
          data[index].color = "#ffffff";
        }    
        graphData.unshift(data[index]);
      }

      //console.log(graphData,'graphDataRRRRRRRRRRRRRR========');
      chart.data = graphData;
      var categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
      categoryAxis.dataFields.category = "name";
      categoryAxis.renderer.grid.template.location = 0;
      categoryAxis.fontSize = 0;
      categoryAxis.dashLength = 0;
      categoryAxis.tickLength = 0;
      categoryAxis.gridAlpha = 0;
      categoryAxis.gridColor = "transparent";
      categoryAxis.axisColor = "#8b8d8d";
      categoryAxis.axisThickness = 50;
      categoryAxis.tooltip.disabled = true;
      categoryAxis.renderer.grid.template.disabled = true;
      var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
      valueAxis.renderer.grid.template.disabled = true;
      valueAxis.fontSize = 0;
      valueAxis.dashLength = 0;
      valueAxis.tickLength = 0;
      valueAxis.gridAlpha = 0;

 
      // Create series
      var series = chart.series.push(new am4charts.ColumnSeries());
      series.dataFields.valueY = "data";
      series.dataFields.categoryX = "name";
      series.columns.template.propertyFields.fill = 'color';
      series.columns.template.propertyFields.stroke = 'color';
      /* Add a single HTML-based tooltip to first series */
      series.columns.template.tooltipHTML = "<div class='chart_hover'><h3>{name}</h3><div class='data_show'><p class='label_sec'>Year:</p><p class='value_sec'>{date}</p></div><div class='data_show'><p class='label_sec'>Population:</p><p class='value_sec'>{valueY}</p></div></div>";
      series.tooltip.label.interactionsEnabled = true;
      series.tooltip.pointerOrientation = "vertical";
    
    
      chart.current = chart;
      return () => {
        chart.dispose();
      }
    })
  }
  useEffect(() => {
    getChartData();
  }, [])
  return (
    <div>
      <div id={'economicoverview_chart'} style={{ width: "300px", height: "300px" }}></div>
    </div>
  )
}
export default EconomicOverviewChart;