// Layouts.js
import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';

import "./Header.css";
import "./Footer.css";
import Header from './Header';
import Footer from './Footer';
/**/
import Home from'../components/home/Home';
import Economicoverview from'../components/economicoverview/Economicoverview';
import Economic from'../components/economic/Economic';
import Workforce from'../components/workforce/Workforce';
import Urban from'../components/urban/Urban';
import Jurisdictional from'../components/jurisdictional/Jurisdictional';
export default class Layouts extends Component {
  render() {
    return (
    <div>
          <Router basename={process.env.PUBLIC_URL}>
               <Header></Header>
                    <Switch>
                        <Route exact path='/' component={Home} />
                        <Route path='/economicoverview' component={Economicoverview} />
                        <Route path='/economic' component={Economic} />
                        <Route path='/workforce' component={Workforce} />
                        <Route path='/urban' component={Urban} />
                        <Route path='/jurisdictional' component={Jurisdictional} />
                    </Switch>
                  <Footer> </Footer>
                </Router>
    </div>
    );
  }
}