//Workforce.js
import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import './Workforce.css';
import TotalEmployeeBySector from './TotalEmployeeBySector';
import AverageAnnualWagesBySector from './AverageAnnualWagesBySector';
import EducationLevel from './EducationLevel';
class Workforce extends Component {
        constructor(props) {
        super(props);
        this.state = {
        pagedata:[],	
        isActive: false,
        };
        }
	
            componentDidMount() {
            fetch('https://communitydashboard.vegas/API/Workforce')
            .then(async response => {
            const data = await response.json();
            if(data){
            this.setState({ pagedata: data })
            }
            })
            .catch(error => {
            // this.setState({ errorMessage: error.toString() });
            console.error('API Error', error);
            });
            }

  render() {
  	  const data = this.state.pagedata && this.state.pagedata && this.state.pagedata;
  	  console.log(data,'MAIN--');
  	 	const rightsection= this.state.pagedata && this.state.pagedata.rightsection; 
  	 	const content = this.state.pagedata && this.state.pagedata.content; 
  	 	const msacontent = this.state.pagedata && this.state.pagedata.msacontent; 
  	 	const date = this.state.pagedata && this.state.pagedata.date;
  	 	const actualdate = this.state.pagedata && this.state.pagedata.actualdate;


  	 	//SECTION 2
  	 	const rightsection1= this.state.pagedata && this.state.pagedata.rightsection1; 
  	 	const average = this.state.pagedata && this.state.pagedata.average; 
  	 	const averagemsa = this.state.pagedata && this.state.pagedata.averagemsa; 
  	 	const date1 = this.state.pagedata && this.state.pagedata.date1;
  	 	const actualdate1 = this.state.pagedata && this.state.pagedata.actualdate1;

  	 	//SECTION 3
  	 	const rightsection2= this.state.pagedata && this.state.pagedata.rightsection2; 
  	 	const education = this.state.pagedata && this.state.pagedata.education; 
  	 	const educationmsa = this.state.pagedata && this.state.pagedata.educationmsa; 
  	 	const date2 = this.state.pagedata && this.state.pagedata.date2;
  	 	const actualdate2 = this.state.pagedata && this.state.pagedata.actualdate2;

    return (
        <div className="py-4 bg-pttern-all">
        	<div className="workforce-inner">
	            <div className="container">
                    <h1>City of Las Vegas <strong>Workforce Summary</strong></h1>
                    {rightsection && msacontent && content && date && actualdate && <TotalEmployeeBySector actualdate={actualdate} date={date} content={content} msacontent={msacontent} rightsection={rightsection} />}
                    {rightsection1 && average && averagemsa && date1 && actualdate1 && <AverageAnnualWagesBySector rightsection1={rightsection1} average={average} averagemsa={averagemsa}  date1={date1}  actualdate1={actualdate1}   />}
                    {rightsection2 && education &&  educationmsa && date2 &&  actualdate2  && <EducationLevel rightsection2={rightsection2} education={education}  educationmsa={educationmsa}  date2={date2}  actualdate2={actualdate2}  />}
                    <div className="note">
                    <p><strong>Note:</strong> The American Community Survey collects Employment statistics for jurisdictions within the United States. Smaller areas with a high margin-of-error are not reported here.</p>
                     </div>
            	</div>
            </div>
        </div>
    );
  }
}

export default Workforce;