import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import SettingImage from  "../../assets/images/home/setting.webp";
import LasVegasChart from './LasVegasChart';
import MSAChart from './MSAChart';
import SidebarMSAChart from './SidebarMSAChart';
import SidebarLasVegasChart from './SidebarLasVegasChart';

export default class Boxes extends Component {

	constructor(props) {
			super(props);
			this.state = {
				msaIsActive: false,
				lasVegasIsActive: true,
				average:this.props.average,
				averagemsa: this.props.averagemsa,
				graphtype:'lasvegas',
				graphId:'lasvegas-chart-2',
				
				date:this.props.date1,
				actualdate:this.props.actualdate1,
				name:this.props.rightsection1.name,
				displaydata:this.props.rightsection1.displaydata,
				PreviousChange:this.props.rightsection1.PreviousChange,
			};
		}


	 handleToggle = (index) => {

	 		if(index.target.id=='msa'){
				var cityormsa='msa';
			}else{
				var cityormsa='city';
			}

			var type='average';
			var date=this.state.actualdate;
			var name=this.state.name;
			name = name.replace('&','%26');
		
			fetch('https://communitydashboard.vegas/API/workforce/right_section?name='+name+'&type='+type+'&cityormsa='+cityormsa+'&date='+date)
			.then(async response => {
			const data = await response.json();
			if(data){

			console.log(data,'API----');
			this.setState({ PreviousChange:data.PreviousChange,displaydata:data.displaydata,name:data.name});
			}
			})
			.catch(error => {
			// this.setState({ errorMessage: error.toString() });
			console.error('API Error 3', error);
			});
			

		 	if(index.target.id=='msa'){
		 		 this.setState({ msaIsActive: true });
		 		 this.setState({ lasVegasIsActive: false });
		 		 this.setState({ averagemsa: this.props.averagemsa });
		 		 this.setState({ graphtype: 'msa' });
		 		 this.setState({ graphId: 'msa-chart-2' });
		 	}else{
				this.setState({ msaIsActive: false });
				this.setState({ lasVegasIsActive: true });
				this.setState({ average: this.props.average});
				this.setState({ graphtype: 'lasvegas' });
				this.setState({ graphId: 'lasvegas-chart-2' });
				this.forceUpdate();
		 	}
	 };

  render() {

	  	const 	average =this.state.average;
	  	const 	averagemsa =this.state.averagemsa;
	  	const 	graphtype =this.state.graphtype;
	  	const 	graphId =this.state.graphId;
	  	const 	msaIsActive = this.state.msaIsActive;
	  	const 	lasVegasIsActive = this.state.lasVegasIsActive;
	  	const 	date=this.state.date && this.state.actualdate ? this.state.date : '';
			const 	actualdate=this.state.actualdate;
			const 	name=this.state.name;
			const 	displaydata=this.state.displaydata;
			const 	PreviousChange=this.state.PreviousChange;
    return (
     <div className="workforce-sec">
	            		<h4>Average Annual Wages by Sector<br/>
	            		<span id={'date2'}>{date && actualdate ? date : ''} | </span><span>City of Las Vegas</span></h4>
	            		<div className="workforce-sec-full">
	            			<div className="workforce-sec-left">
			            		<div className="workforce-graph-img">

										{graphtype==='msa' ? (
										average && <MSAChart data={average} graphtype={graphtype}  graphId='{graphId}' />
										) : (
										average  && <LasVegasChart data={average} graphtype={graphtype}  graphId={graphId} />
										)}
				            		</div>
			            		<p className="right_">Click a sector to view historical data on right</p>
		            		</div>
		            		<div className="workforce-sec-right">
		            			<h4>{name ? name : ''}</h4>
								<h6>Avg. Annual Wages</h6>
								<div className="box-bottom">
									<div className="inner-box">
										<h2>{displaydata ? displaydata : ''}</h2>
										<h5>								
											<div className="ranking-style">{PreviousChange ? PreviousChange : ''}</div>
											<img src="../assets/images/workforce/pink-down-arw.png" />
										</h5>
										<span>% Change vs. Prior Period</span>
									</div>
									<div className="graph-sec">
								{graphtype==='msa' ? (
									this.props.rightsection1.cache && graphtype && <SidebarMSAChart data={this.props.rightsection1} graphtype={graphtype}  graphId={'msa-sidebar-chart-2'} />
									) : (
									this.props.rightsection1.cache && graphtype &&  <SidebarLasVegasChart data={this.props.rightsection1} graphtype={graphtype}  graphId={'lasvegas-sidebar-chart-2'} />
									)}									</div>
									<ul className="graph-indicator">
										<li id={'msa'} className={msaIsActive ? "active" : null} onClick={this.handleToggle.bind('msa')}><span className="bx grey-bx"></span> Las Vegas MSA</li>
										<li id={'lasvegas'} className={lasVegasIsActive ? "active" : null}  onClick={this.handleToggle.bind('lasvegas')}><span className="bx pink-bx"></span> City of Las Vegas</li>								
									</ul>
								</div>	
		            		</div>
	            		</div>
	            	</div>

    );
  }
}