import React, { useEffect } from 'react'
var DateFormat = require("dateformat");
const SidebarMSAChart = (props) => {
  const getChartData = () => {

    //console.log(props.data,'data--------');


    Promise.all([
      import("@amcharts/amcharts4/core"),
      import("@amcharts/amcharts4/charts"),
      import("@amcharts/amcharts4/themes/animated"),
    ]).then((modules) => {
	  let am4core = modules[0];
	  let am4charts = modules[1];
	  am4core.addLicense("CH259299157");
	  let chart = am4core.create(props.graphId, am4charts.XYChart);
	  chart.paddingRight = 20;
	  let data = props.data; 
    chart.data = data;
     chart.numberFormatter.numberFormat = "#a";
    chart.numberFormatter.bigNumberPrefixes = [
    { number: 1000, prefix: "K" },
    { number: 1000000, prefix: "M" },
    { number: 1000000000, prefix: "B" },
    { number: 1000000000000, prefix: "T" },
    { number: 1000000000000000, prefix: "P" },
    { number: 1000000000000000000, prefix: "E" },
    { number: 1e21, prefix: "Z" },
    { number: 1e24, prefix: "Y" },
    ];
    var categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "date";
    categoryAxis.renderer.grid.template.location = 0;
    //categoryAxis.gridColor = "transparent";
    categoryAxis.renderer.inversed = true;
    categoryAxis.fontSize = 0;
    categoryAxis.dashLength = 0;
    categoryAxis.tickLength = 0;
    categoryAxis.gridAlpha = 0;
    categoryAxis.renderer.grid.template.disabled = true;
    var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.grid.template.disabled = true;
    valueAxis.fontSize = 0;
    valueAxis.dashLength = 0;
    valueAxis.tickLength = 0;
    valueAxis.gridAlpha = 0;
    // Create series
    var series = chart.series.push(new am4charts.ColumnSeries());
    series.dataFields.valueY = "data2";
    series.dataFields.categoryX = "date";
    //series.name = "Visits";
    series.columns.template.tooltipText = "[bold]{valueY}[/]";
    //series.columns.template.fillOpacity = .8;
    series.columns.template.fill = am4core.color("#f4be49");
    series.columns.template.stroke = am4core.color("#f4be49");
    var columnTemplate = series.columns.template;
    columnTemplate.strokeWidth = 4;
    //columnTemplate.strokeOpacity = 1;
    
  chart.current = chart;
  return () => {
    chart.dispose();
  }
    })
  }
  useEffect(() => {
    getChartData();
  }, [])
  return (
  	
    <div>
      <div id={props.graphId} style={{  height: "84px" }}></div>
    </div>
  )
}
export default SidebarMSAChart;