import React, { Component } from 'react'  
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";  
var DateFormat = require("dateformat");
export default class EconomicUpdateChart extends Component {
  constructor(props) {
    super(props);
    }
componentDidMount() {
  console.log(this.props.data);
    am4core.addLicense("ch-custom-attribution");
    let chart = am4core.create("economicoverview_update", am4charts.XYChart);
    chart.paddingRight = 20;
    let data=this.props.data;

       var graphData = [];
      for (var index in data) {
        var date = DateFormat(index);
        data[index].date = date;      
        data[index].v = data[index].v;  
        graphData.unshift(data[index]);
      }
    chart.data = graphData;
    var categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "date";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.fontSize = 0;
    categoryAxis.dashLength = 0;
    categoryAxis.tickLength = 0;
    categoryAxis.gridAlpha = 0;
    categoryAxis.axisThickness = '2';
    categoryAxis.tooltip.disabled = true;
    categoryAxis.renderer.grid.template.disabled = true;
   
    var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.grid.template.disabled = true;
    valueAxis.fontSize = 0;
    valueAxis.dashLength = 0;
    valueAxis.tickLength = 0;
    valueAxis.gridAlpha = 0;

    // Create series
    var series = chart.series.push(new am4charts.ColumnSeries());
    series.dataFields.valueY = "v";
    series.dataFields.categoryX = "date";

   // series.name = "Visits";
    series.columns.template.tooltipText = "{categoryX}: [bold]{valueY}[/]";
    series.columns.template.tooltipText = "{valueY}";
    series.columns.template.fillOpacity = .8;
    series.columns.template.fill = am4core.color("#8b8d8d");
    series.columns.template.stroke = am4core.color("#8b8d8d");
    
  }

  componentWillUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
  } 
    render() {  

        return (  
            <div>  
            <div  id={'economicoverview_update'} style={{ width: "300px", height: "300px" }}></div>
            </div>  
        )  
    } 

}