import React, { Component } from 'react'  
import * as am4core from "@amcharts/amcharts4/core";  
import * as am4charts from "@amcharts/amcharts4/charts";  
import am4themes_animated from "@amcharts/amcharts4/themes/animated";  

export default class Sales extends Component {  
  
    componentDidMount() {  
        am4core.useTheme(am4themes_animated);
        // Create chart instance
        var chart = am4core.create("3686", am4charts.XYChart);
        var data=this.props.data1;
            var graphData = [];
            for (var index in data) {
            var date = index;
            data[index].date = date;      
            data[index].data1 = data[index].v*100;  
            data[index].data2 = this.props.data2[index].v*100; 
            data[index].data3 = this.props.data3[index].v*100;

            graphData.unshift(data[index]);
            }

        //var categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
        ///categoryAxis.tooltip.disabled = true;
        ///categoryAxis.renderer.grid.template.disabled = true;


        chart.data =  graphData;
        var dateAxis = chart.xAxes.push(new am4charts.DateAxis());
       // dateAxis.renderer.grid.template.location = 10;
        dateAxis.renderer.grid.template.disabled = true;
        var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
        // Create series
        function createSeries(field, name, hiddenInLegend) {
        var series = chart.series.push(new am4charts.LineSeries());
        series.dataFields.valueY = field;
        series.dataFields.dateX = "date";
        series.strokeWidth = 2;
        series.yAxis = valueAxis;
        series.name = name;
        series.tooltipText = "{name}: [bold]{valueY}[/]";
        series.tensionX = 0.8;
        series.showOnInit = true;


        return series;
        }

        var series1 = createSeries("data1", "United States", true);
        var series2 = createSeries("data2", "Las Vegas (NV)", true);
        var series3 = createSeries("data3", "Las Vegas MSA (NV)", true);

        chart.legend = new am4charts.Legend();
        chart.cursor = new am4charts.XYCursor();

    }  
  
    componentWillUnmount() {  
        if (this.chart) {  
            this.chart.dispose();  
        }  
    }  
    render() {  
        return (  
            <div>  
            <div style={{  height: "220px" }} id='3686'></div>
            </div>  
        )  
    }  
}